.add-option-error{
    color: $off-white;
    font-style: italic;
    margin: $m-size 0 0 0;
    padding: 0 $m-size;
}

.add-option{
    display: flex;
    flex-direction:column;
    padding:$m-size;

}

.add-option__input{
    flex-grow:1;
    background:$dark-blue;
    border: none;
    color: white;
    border-bottom:.3rem solid darken($dark-blue,10%);
    //margin-right:$s-size;
    margin:0 0 $s-size 0;
    padding:$s-size;
}

@media (min-width:$desktop-breakpoint){
    .add-option{
        flex-direction: row;
    }
    .add-option__input{
        margin:0 $s-size 0 0;
    }
    
}