.widget{
    background:$light-blue;
    margin-bottom:$xl-size;
}

.widget-message {
    border-bottom: 1px solid lighten($light-blue,10%);
    color: $off-white;
    
    margin:0;
    padding:$l-size;
    text-align:center;
}
.widget-header{
    background:$blue;
    color:$off-white;
    padding:$m-size;
    display:flex;
    justify-content:space-between;
}

.widget-header__title{
    margin:0;
}